.gpt4_features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}

.gpt4_features-container_feature-title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;

}

.gpt4_features-container_feature-title h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 20px;
    line-height: 24px;

    letter-spacing: 0.04em
}

.gpt4_features-container_feature-title div{
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 54px green;
    margin-bottom: 0.25rem;
}

.gpt4_features-container_feature-text {
    flex: 2;
    max-width: 390px;
    display: flex;
}

.gpt4_features-container_feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .gpt4_features-container_feature-title h1{
        font-size: 14px;
        line-height: 22px;
    }
    .gpt4_features-container_feature-title p{
        font-size: 12px;
        line-height: 20px;
    }
    .gpt4_features-container_feature {
        margin: 1rem 0;
    }
    
}