.gpt4_brand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.gpt4_brand div{
    flex: 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem;

    display: flex;
    justify-content: center;
    align-items: center;
    
    width: 10px;
}

.gpt4_brand img{
    width: 100%;
    padding: 0px 20px;
}