.gpt4_blog-container_article {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.gpt4_blog-container_article-image {
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}

.gpt4_blog-container_article-image img{
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}
.gpt4_blog-container_article-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
}

.gpt4_blog-container_article-content p{
font-size: 11.649px;
font-family: var(--font-family);
font-weight: 700;
line-height: 34.947px;
}

.gpt4_blog-container_article-content h3{
font-size: 25.11px;
font-family: var(--font-family);
font-style: normal;
font-weight: 800;
line-height: 30.287px;
margin-bottom: 5rem;
}

.gpt4_blog-container_article-content p:last-child{
    cursor: pointer;
}

@media screen and (max-width: 550px) {
    .gpt4_blog-container_article-content h3{
       font-size: 18px;
       line-height: 25px; 
    }
}